import React from 'react';
import {Divider, IconButton, List, Text} from 'react-native-paper';
import {FolderType, Media} from '../../../types';
import {
  NavigationProp,
  useNavigation,
} from '@react-navigation/native';
import {ProjectsStackedParams} from '../../../types/nav';
import {useWindowDimensions} from 'react-native';
const maxTitleLengthMobile = 20;
const maxTitleLengthDesktop = 70;
function ProjectItem({
  item,
  folderType,
  onRemoveFolder,
  width,
}: {
  item: Media;
  folderType: FolderType;
  onRemoveFolder: (item: {id: string}) => void;
  width: number;
}) {
  const navigation =
    useNavigation<NavigationProp<ProjectsStackedParams>>();
  const projectType =
    folderType === FolderType.encoder
      ? 'EncoderProject'
      : 'EditorProject';
  const projectIcon =
    folderType === FolderType.editor ? 'film' : 'matrix';
  const isMobile = width < 700;
  const maxTitleLength = isMobile
    ? maxTitleLengthMobile
    : maxTitleLengthDesktop;
  return (
    <>
      <Divider />
      <List.Item
        style={{padding: 19, width}}
        titleMaxFontSizeMultiplier={0.5}
        titleNumberOfLines={2}
        descriptionNumberOfLines={2}
        title={item.title}
        onPress={() => {
          navigation.navigate({
            name: projectType,
            params: {
              projectId: item.id,
              mnemonic: item.mnemonic,
              parentFolderId: item.folderId,
            },
          });
        }}
        left={() => <List.Icon icon={projectIcon} />}
        right={() => (
          <IconButton
            icon={'delete'}
            onPress={() => onRemoveFolder(item)}
          />
        )}
      />
    </>
  );
}

export default ProjectItem;
